// Шрифты + normalize ↓
@import "fonts+normalize";
// Переменные ↓
@import "vars";
// Миксины ↓
@import "mixins";
// Плагины и библиотеки CSS/SASS ↓
@import "libs";
// Подключаем спрайты (просто раскомментируем @import при появлении PNG спрайтов) ↓
// @import "sprite"

/* 1. MAIN ================== */

.main {
	position: relative;
	overflow: hidden;
	min-height: 100%; }

.wrapper {
	position: relative;
	max-width: 1200px;
	min-width: 320px;
	padding: 0 15px;
	margin-right: auto;
	margin-left: auto; }

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto; }

/* PRELOADER ================== */

#logo {
	animation-duration: 3s; }

$color-1: #ECD078;
$color-2: #C02942;
$color-3: #542437;
$color-4: #53777A;

.loaderArea {
	width: 100vw;
	background: #ececec;
	overflow: hidden;
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 99999999; }

.loader {
	&:before,
	&:after {
		position: absolute;
		top: 50%;
		left: 50%;
		border-radius: 50%;
		border-style: solid;
		border-top-color: $color-1;
		border-right-color: $color-2;
		border-bottom-color: $color-3;
		border-left-color: $color-4;
		content: '';
		transform: translate(-50%, -50%);
		animation: rotate 1.5s  infinite ease-in-out; }
	&:before {
		border-width: 6vh; }
	&:after {
		width: 20vh;
		height: 20vh;
		border-width: 2vh;
		animation-direction: reverse; } }

@keyframes rotate {
	0% {
		transform: translate(-50%, -50%) rotate(0); }
	100% {
		transform: translate(-50%, -50%) rotate(360deg); } }

/* PRELOADER END ================== */

/* Стилизация полосы прокрутки ================== */

::-webkit-scrollbar-button {
	background-image: url ('');
	background-repeat: no-repeat;
	width: 6px;
	height: 0px; }

::-webkit-scrollbar-track {
	background-color: $main; // Цвет дорожки, по которой двигается бегунок прокрутки
	box-shadow: 0px 0px 3px #000 inset; }

::-webkit-scrollbar-thumb {
	border-radius: 2px; // Закругление бегунка
	background-color: $accent; // Цвет бегунка
	background-image: url("../img/icon_for_scrol.png");
	background-position: center;
	background-repeat: no-repeat; }

::-webkit-scrollbar-thumb:hover {
	background-color: lighten($accent, 10%); } // Цвет бегунка при наведении на него курсора

::-webkit-resizer {
	background-image: url('');
	background-repeat: no-repeat;
	width: 7px;
	height: 0px; }

::-webkit-scrollbar {
	width: 10px; } // Основная ширина полосы прокрутки

// For Internet Explorer
body {
	scrollbar-face-color: $accent;
	scrollbar-track-color: $main;
	scrollbar-shadow-color: $accent;
	scrollbar-arrow-color: $accent; }

/* 2. BUTTON ================== */

.btn, a.btn {
	display: inline-block;
	border: none;
	color: #fff;
	text-decoration: none;
	background-color: $accent;
	padding: 24px;
	font-size: 18px;
	text-transform: uppercase;
	border-radius: 3px;
	text-align: center;
	position: relative;
	outline: none;
	transition: background-color .1s ease;
	&::after {
		transition: background-color .2s ease;
		position: absolute;
		content: '';
		height: 5px;
		bottom: 0;
		width: 100%;
		background-color: darken($accent, 50%);
		opacity: .18;
		border-bottom-left-radius: 2px;
		border-bottom-right-radius: 2px;
		left: 0; }
	&:focus, &:hover {
		text-decoration: none;
		color: #fff; }
	&:hover {
		background-color: lighten($accent, 10%);
		&::after {
			opacity: .22; } }
	&:active {
		background-color: darken($accent, 5%);
		&::after {
			opacity: .32; } } }

input[type="submit"].btn, button.btn {
	padding-top: 0; }

/* 3. FORM ================== */

input[type="text"],
input[type="password"],
input[type="tel"],
input[type="email"],
textarea {
	height: 38px;
	padding: 0 15px;
	font-family: "TimesNewRomanBold";
	font-size: 12px;
	line-height: 21px;
	text-transform: uppercase;
	@include placeholder-color(rgba(#333333, 0.45));
	background: transparent;
	border: 1px solid #000;
	&.form-control {
		display: block;
		width: 100%; } }

textarea {
	resize: none;
	height: auto;
	min-height: 100px;
	padding-top: 10px; }

label {
	display: block; }

/* 4. HEADER ================== */

.header {
	width: 100vw;
	height: 500px;
	padding-top: 27px;
	background-image: url(../img/header_bg.jpg);
	background-color: #E9E1D4;
	overflow: hidden;
	position: relative;
	@include rbg; }

.menu {
	width: 760px;
	margin: 0 auto;
	&__left {
		float: left;
		margin-top: 57px; }
	&__right {
		float: right;
		margin-top: 57px; }
	&__items {
		list-style-type: none;
		float: left;
		padding: {
			left: 3px;
			right: 3px; }
		margin-right: 25px;
		border-bottom: 3px solid transparent;
		transition: border-color 0.5s ease-out;
		color: #1a1a1a;
		font-family: "TimesNewRomanRegular";
		font-size: 12px;
		font-weight: 400;
		line-height: 24px;
		text-transform: uppercase; }
	&__items--last {
		margin-right: 7px; }
	&__items:hover {
		border-color: #1a1a1a; }
	&__logo {
		display: block;
		width: 260px;
		position: absolute;
		margin-left: auto;
		margin-right: auto;
		left: 0;
		right: 0;
		opacity: 1; } }

/* 5. CONTENT ================== */

/* ABOUT US ================== */

.about-us {
