		background-image: url(../img/about_us.jpg);
		@include rbg;
		overflow: hidden;
		float: left;
		position: relative;
		padding: {
			top: 170px;
			bottom: 70px;
			left: 85px;
			right: 85px; } }
	&__right {
		width: 50%;
		min-height: 500px;
		float: right;
		padding: {
			top: 170px;
			bottom: 70px;
			left: 85px;
			right: 85px; }
