.title-in-section {
	color: #010101;
	font-family: "TimesNewRomanBold";
	font-size: 36px;
	font-weight: 700;
	text-transform: uppercase;
	border: 10px solid #010101;
	width: 300px;
	text-align: center;
	line-height: 80px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%); }

.title-in-section--contact-us {
	position: static;
	transform: translate(0%, 0%);
	margin: 0 auto; }

/* ABOUT US END ================== */

/* GALLERY SMALL ================== */

.gallery-1 {
	width: 100vw;
	background-color: #F2EFE5;
	overflow: hidden; }

.gallery-1__item {
	position: relative;
	display: block;
	float: left;
	width: 16.6666666666%;
	overflow: hidden;
	img {
		display: block;
		width: 100%;
		height: auto;
		@include tr(.5s); }
	.overlay {
		position: absolute;
		top: 0;
		left: 0;
		color: #fff;
		width: 100%;
		height: 100%;
		background-color: rgba(#CCC8B6, .66);
		opacity: 0;
		@include tr(.8s);
		text-align: center;
		&:before {
			display: inline-block;
			height: 100%;
			vertical-align: middle;
			content: ""; }
		i {
			color: $main;
			font-size: 40px;
			@include tr(1s);
			transform: scale(0.1);
			vertical-align: middle;
			&:hover {
				color: #F2EFE5;
				@include tr(1s); } } }
	&:hover .overlay {
		opacity: 1; }
	&:hover i {
		transform: scale(1.1);
		@include tr(1s); }
	&:hover img {
		transform: scale(1.1); } }

.fancybox-button, .fancybox-infobar__body, .fancybox-arrow:before, .fancybox-arrow:after {
	background-color: $accent; }
.fancybox-button:hover:not([disabled]) {
	background-color: rgba($accent, 0.8); }
.fancybox-can-zoomIn .fancybox-image-wrap {
	cursor: default; }
.fancybox-container {
	touch-action: manipulation; }

/* GALLERY SMALL END ================== */

/* QUOTE ================== */

.bg {
	background-color: black;
	z-index: -5; }

.quote {
	width: 100vw;
	height: 350px;
	background-image: url(../img/quote_bg.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%;
	overflow: hidden;
	padding: 0 15px;
	position: relative;
	&__text {
		color: #f2efe5;
		font-family: "TimesNewRomanBoldItalic";
		font-size: 24px;
		line-height: 35px;
		max-width: 911px;
		text-align: center;
		display: table;
		position: absolute;
		top: 43%;
		transform: translateY(-50%);
		margin-left: auto;
		margin-right: auto;
		left: 0;
		right: 0;
		.quote__quotes-l {
			font-family: "TimesNewRomanItalic";
