.team__subscription-input:focus::-webkit-input-placeholder {
	opacity: 0;
	transition: opacity 0.3s ease; }
.team__subscription-input:focus::-moz-placeholder {
	opacity: 0;
	transition: opacity 0.3s ease; }
.team__subscription-input:focus:-moz-placeholder {
	opacity: 0;
	transition: opacity 0.3s ease; }
.team__subscription-input:focus:-ms-input-placeholder {
	opacity: 0;
	transition: opacity 0.3s ease; }

.triangle {
	display: block;
	@include arr(40,60, #CCC8B6, l);
	position: absolute;
	right: 0;
	top: 50%;
