@import "vars";
@import "mixins";

/* 1. FONTS */
/* ================================================ */

@include font-face("OpenSans-Regular", "../fonts/OpenSans-Regular/OpenSans-Regular");
@include font-face("Roboto-Regular", "../fonts/Roboto-Regular/Roboto-Regular");
@include font-face("TimesNewRomanBold", "../fonts/TimesNewRomanBold/TimesNewRomanBold", bold);
@include font-face("TimesNewRomanBoldItalic", "../fonts/TimesNewRomanBoldItalic/TimesNewRomanBoldItalic", bold, italic);
@include font-face("TimesNewRomanItalic", "../fonts/TimesNewRomanItalic/TimesNewRomanItalic", normal, italic);
@include font-face("TimesNewRomanRegular", "../fonts/TimesNewRomanRegular/TimesNewRomanRegular");

/* 2. NORMALIZE */
/* ================================================ */

html, body, div, span, applet, main, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%; }

/* HTML5 display-role reset for older browsers*/
article, aside, details, main, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block; }

body {
	margin: 0px;
	padding: 0px;
	font-family: $default-font;
	line-height: 1;
	background-color: #000;
	height: 100%;
	position: relative;
	font-size: 14px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

blockquote, q {
	quotes: none; }

blockquote {
	&:before, &:after {
		content: '';
		content: none; } }

q {
	&:before, &:after {
		content: '';
		content: none; } }

table {
	border-collapse: collapse;
	border-width: 0px;
	padding: 0px;
	margin: 0px; }

html {
	height: 100%;
	-webkit-text-size-adjust: none;
	-ms-text-size-adjust: none; }

input, textarea {
	color: #333;
	outline: none;
	border-radius: 0;
	-moz-border-radius: 0;
	-webkit-border-radius: 0;
	-webkit-appearance: none; }

input {
	&[type="button"], &[type="submit"] {
		cursor: pointer;
		-webkit-appearance: none;
		outline: none; } }

button {
	cursor: pointer;
	-webkit-appearance: none;
	outline: none; }

td, form {
	margin: 0px;
	padding: 0px; }

a {
	color: #000;
	@include tr(all 0.2s linear);
	text-decoration: none;
	outline: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	&:before, &:after {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box; } }
